    import {  
        mapState,  
        mapMutations  
    } from 'vuex'; 
	export default {
		data() {
			return {
				inmoney:"",
				discount_money:0,
				step:0,
				store_name:'',
				store_avatar:'',
				store_id:0,
				payType: 3,
				paycode:'pdpay',
				memberInfo: {},
				maskState: 0, //优惠券面板显示状态
				couponList: [],
				selectCouponId:0,
				selectCouponTxt:'选择优惠券',
				logining: false,				
				password: '',
				mask: false,
				passwordArray: [],
				pwdnum:0,
				bott: '',
				pasList: ['', '', '', '', '', ''],
				numbr: [1, 2, 3, 4, 5, 6, 7, 8, 9],
				pay_comment:''
			};
		},
        computed: mapState(['hasLogin','userInfo']),
		onLoad(e) {
			console.log('---------e-----------');
			console.log(e);
			if(e.q){
				let rqurl = decodeURIComponent(e.q);
				this.store_id = this.$Request.getQueryString(rqurl,'pay_sid');
			}else{
				this.store_id=e.store_id;
			}
			console.log('==storeid===='+this.store_id);
			this.loadData(e);
		},

		methods: {   
            async loadData(e) {  
				let _this = this;
				console.log('===userInfo==='+this.hasLogin);
				console.log('---------e-----------');
				console.log(e);
				console.log(this.userInfo);
				console.log(this.userInfo.token);
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'/pages/member/login',
					})
                }else{
					this.$Request.post('member_buy/store_info',{store_id:_this.store_id}).then(res => {
						if (res.code == 200) {
							console.log(res);
							//this.login(res.datas);
							_this.store_name = res.datas.store_name;
							_this.store_avatar = res.datas.store_avatar;
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							
						}
					});
				}
            },
			//选择支付方式
			changePayType(type) {
				this.payType = type;
				if(type==1){this.paycode='wxminipay';}
				if(type==3){this.paycode='pdpay';}
			},
			stopPrevent(e){
				console.log('======stopPrevent=====');
				console.log(e);
			},
			selectcoupon(e){
				console.log('======selectcoupon=====');
				this.selectCouponId=e.voucher_id;
				this.discount_money=e.voucher_price;
				this.selectCouponTxt=e.mtitle;
				this.toggleMask();
			},
			//确认支付
			confirm: async function() {
				var _this=this;
				uni.showModal({
					title: '提示',
					content: '是否确认支付当前订单',
					success: function (res) {
						if (res.confirm) {
							
							if(_this.paycode=='wxminipay'){
								 uni.login({
									provider: 'weixin',
									success: function(loginRes) {
										let code = loginRes.code;
										console.log(code);
										_this.toPostPay(code);
									}
									
								});	
							}else{
								//_this.toPostPay('');
								if(_this.memberInfo.pp_state==0){
									uni.showToast({
										icon: 'none',
										title:'您未设置支付密码，请到个人中心—>安全设置里进行设置',
										duration:2000,
										success: function () {
											setTimeout(function () {
													
												 }, 2000);
											}
									});
								}else{
									_this.masks();return;
									
								}
							}
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				}); 
			},
			toPostPay(code){
				let _this = this;
				_this.logining = true;
				let postData={
					store_id:this.store_id,total:this.inmoney,vt_id:this.selectCouponId,paycode:this.paycode,wxcode:code,ppwd:this.password,pay_comment:this.pay_comment
				}
				console.log(postData);
				this.$Request.post('member_buy/buy_step2',postData).then(res => {
					if (res.code == 200) {
						console.log(res);
						if(res.datas.payment_code == 'wxminipay' ){
							console.log('调起支付');
								// 调起支付
								uni.requestPayment({
									provider: 'wxpay',
									timeStamp: res.datas.payinfo.timeStamp+'',
									nonceStr: res.datas.payinfo.nonceStr+'',
									package: res.datas.payinfo.package+'',
									signType: res.datas.payinfo.signType+'',
									paySign: res.datas.payinfo.paySign+'',
									success: function(re) {
										console.log('success:' + JSON.stringify(re));
										uni.showToast({
											icon: 'none',
											title:'支付成功',
											duration:2000,
											success: function () {
												setTimeout(function () {
														uni.navigateTo({
															url:"/pages/store/index?store_id="+_this.store_id,
														});
													 }, 2000);
												}
										});
									},
									fail: function(err) {
										console.log('fail:' + JSON.stringify(err));
										_this.logining = false;
									}
								});
						}else{
							uni.showToast({
								icon: 'none',
								title:'支付成功',
								duration:2000,
								success: function () {
									setTimeout(function () {
											uni.navigateTo({
												url:"/pages/store/index?store_id="+_this.store_id,
											});
										 }, 2000);
									}
							});
						}
					/* 	uni.navigateTo({
							url:"/pages/member/home"
						}) */
						//this.login(res.datas);
						//this.store_name = res.datas.store_name;
					}else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						_this.logining = false;
					}
				});
			},
			async nextPay(){
				let _this = this;
				if(this.inmoney.length<1){
					uni.showToast({
						icon: 'none',
						title:'请输入消费金额！',
						duration:2000
					});
					return false;
				}
				_this.logining = false;
				_this.$Request.post('member_buy/buy_step1',{store_id:this.store_id,total:this.inmoney}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.memberInfo=res.datas.member_info;
						_this.couponList=res.datas.voucher_list;
						_this.inmoney=res.datas.cost_total;
						
						//this.login(res.datas);
						//this.store_name = res.datas.store_name;
						_this.step=2;
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						
					}
				});
				
			},
			//显示优惠券面板
			toggleMask(type){
				let timer = type === 'show' ? 10 : 300;
				let	state = type === 'show' ? 1 : 0;
				this.maskState = 2;
				setTimeout(()=>{
					this.maskState = state;
				}, timer)
			},
			// 密码框
			checkNum(num) {
				let _this = this;
				if (this.pwdnum < 6) {
					console.log('---'+num);
					this.passwordArray.push(num+'');
					this.pwdnum++;
					//this.passwordArray=[this.passwordArray,num];
					console.log(this.passwordArray);
					
				}
				if (this.pwdnum == 6) {
					let str = '';
					for (let i = 0; i < this.passwordArray.length; i++) {
						str += this.passwordArray[i];
					}
					console.log(str)
					this.password = str
					this.mask = false
					this.passwordArray = []
					this.bott = '';
					console.log(this.password);
					
					_this.toPostPay('');
					
					this.pwdnum=0;
				}
			},
			reset() {
				this.passwordArray = [];
				this.pwdnum=0;
			},
			backspace() {
				this.passwordArray.pop();
				this.pwdnum--;
			},
			masks() {
				let _this = this
				this.mask = true
				this.pwdnum=0;
				setTimeout(()=> {
					_this.bott = 'bot'
				}, 50)
			},
			maskss() {
				this.mask = false
				this.bott = ''
				this.passwordArray = []
				this.pwdnum=0;
			},
			
		}
	}
